import { Directive, ElementRef, Input, OnInit } from "@angular/core";
import { markdown } from "markdown";

@Directive({
    selector: "[markdown]"
})
export class MarkdownDirective implements OnInit {
    @Input("markdown") mdStr: String = "";

    private md: markdown = markdown;

    constructor(private el: ElementRef){
        
    }

    ngOnInit() {
        console.info(this.mdStr)
        this.el.nativeElement.innerHTML = this.md.toHTML(this.mdStr);
    }
}